.Logo
{
    display:flex;
}
.Logo img
{
 width: 100px;
 height: auto;
}

.MPOptions li
{
  display:flex;
  list-style: none;
  padding: 0;
  justify-content: center;
  margin-top: -50px;
}

.ImageCard img
{
  width: 60px;
  height: 50px;
}

.RestaurantCard {
    display: flex;
    flex-direction: column;
    align-items: left;
    width: 200px; 
    background-color: #ffffff; 
    border-radius: 10px; 
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); 
    margin-top: 40px; 
    padding: 20px; 
  }
  
  .ImageCard img {
    width: 100%; 
    height: 200px; 
    border-bottom: 2px solid #eee; 
  }
  
  .RestaurantCard h1, .RestaurantCard h2, .RestaurantCard h3 {
    margin: 10px 0; 
    color: #333; 
    text-align: left; 
  }
  

  .RestaurantCard h1 {
    font-size: 18px; 
    font-weight: bold;
  }
  
 
  .RestaurantCard h2 {
    font-size: 14px; 
  }
  
  
  .RestaurantCard h3 {
    font-size: 14px; 
    color: #ff9900; 
  }

  .ListLoading
  {
    display: flex;
    flex-wrap: wrap;
    justify-content:space-between;
  }

  .shimmer-wrapper {
    display: flex;
    flex-wrap: wrap;
  }
  
  .shimmer-card {
    width: 250px;
    height: 300px;
    background: linear-gradient(to right, #f0f0f0 8%, #e0e0e0 18%, #f0f0f0 33%);
    background-size: 1000px 100%;
    animation: shimmer 1.5s infinite;
    margin: 10px;
    border-radius: 8px;
  }
  
  @keyframes shimmer {
    0% {
      background-position: -1000px 0;
    }
    100% {
      background-position: 1000px 0;
    }
  }
  
.LoginBtn {
   margin-left: 100px;
}

